<template>
    <form @submit.prevent="submitForm">
        <div class="form-control" :class="{ invalid: !coachFirstName.isValid }">
            <label for="coachFirstName">First name</label>
            <input id="coachFirstName" name="coachFirstName" type="text" v-model.trim="coachFirstName.val"
                @blur="validateInput" />
            <p v-if="!coachFirstName.isValid">First name must not be empty.</p>
        </div>
        <div class="form-control" :class="{ invalid: !coachLastName.isValid }">
            <label for="coachLastName">Last name</label>
            <input id="coachLastName" name="coachLastName" type="text" v-model.trim="coachLastName.val"
                @blur="validateInput" />
            <p v-if="!coachLastName.isValid">Last name must not be empty.</p>
        </div>
        <div class="form-control" :class="{ invalid: !coachDescription.isValid }">
            <label for="coachDescription">Description</label>
            <textarea id="coachDescription" name="coachDescription" type="text" row="5" v-model.trim="coachDescription.val"
                @input="validateDescription"></textarea>
            <p v-if="!coachDescription.isValid">Description must not be empty.</p>
        </div>
        <div class="form-control" :class="{ invalid: !coachHourlyRate.isValid }">
            <label for="coachHourlyRate">Hourly rate</label>
            <input id="coachHourlyRate" name="coachHourlyRate" type="number" v-model="coachHourlyRate.val"
                @change="validateRate" />
            <p v-if="!coachHourlyRate.isValid">Rate must be greater than $0.</p>
        </div>
        <div class="form-control" :class="{ invalid: !coachAreas.isValid }">
            <label for="coachAreas">Areas?</label>
            <div>
                <input id="frontend" name="coachAreas" type="checkbox" value="frontend" v-model="coachAreas.val" />
                <label for="frontend">FrontEnd</label>
            </div>
            <div>
                <input id="backend" name="coachAreas" type="checkbox" value="backend" v-model="coachAreas.val" />
                <label for="backend">BackEnd</label>
            </div>
            <div>
                <input id="career" name="coachAreas" type="checkbox" value="career" v-model="coachAreas.val" />
                <label for="career">Career</label>
            </div>
            <p v-if="!coachAreas.isValid">At least one expertise must be selected.</p>
        </div>
        <p v-if="!formIsValid">Please fix the above errors and submit again</p>
        <base-button class="btn-center">Register</base-button>
    </form>
</template>
<style scoped>
.form-control {
    margin: 0.5rem 0;
}

.btn-center {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

label {
    font-weight: bold;
    display: block;
    margin-bottom: 0.5rem;
}

input[type='checkbox']+label {
    font-weight: normal;
    display: inline;
    margin: 0 0.5rem 0 0;
}

input,
textarea {
    display: block;
    width: 100%;
    border: 1px solid #ccc;
    font: inherit;
}

input:focus,
textarea:focus {
    background-color: #f0e6fd;
    outline: none;
    border-color: #3d008d;
}

input[type='checkbox'] {
    display: inline;
    width: auto;
    border: none;
}

input[type='checkbox']:focus {
    outline: #3d008d solid 1px;
}

h3 {
    margin: 0.5rem 0;
    font-size: 1rem;
}

.invalid label {
    color: red;
}

.invalid input,
.invalid textarea {
    border: 1px solid red;
}
</style>
<script>
export default {
    emits: ['save-data'],
    data()
    {
        return {
            coachFirstName: {
                val: '',
                isValid: true
            },
            coachLastName: {
                val: '',
                isValid: true
            },
            coachAreas: {
                val: [],
                isValid: true
            },
            coachDescription: {
                val: '',
                isValid: true
            },
            coachHourlyRate: {
                val: null,
                isValid: true
            },
            formIsValid: true
        };
    },
    methods: {
        validateForm()
        {
            this.formIsValid = true;
            if (this.coachFirstName.val === '') {
                this.coachFirstName.isValid = false;
                this.formIsValid = false;
            }
            if (this.coachLastName.val === '') {
                this.coachLastName.isValid = false;
                this.formIsValid = false;
            }
            if (this.coachDescription.val === '') {
                this.coachDescription.isValid = false;
                this.formIsValid = false;
            }
            if (!this.coachHourlyRate.val || this.coachHourlyRate < 0) {
                this.coachHourlyRate.isValid = false;
                this.formIsValid = false;
            }
            if (this.coachAreas.val.length === 0) {
                this.coachAreas.isValid = false;
                this.formIsValid = false;
            }
        },
        validateDescription()
        {
            if (this.coachDescription.val.length > 180) {
                const str = this.coachDescription.val
                this.coachDescription.val = str.slice(0, -1);
            }
        },
        validateRate()
        {
            if (this.coachHourlyRate.val <= 4) {
                this.coachHourlyRate.val = 5
            } else if (this.coachHourlyRate.val > 50) {
                this.coachHourlyRate.val = 50
            }
        },
        submitForm()
        {
            this.validateForm();
            if (!this.formIsValid) {
                return;
            }
            const formData = {
                id: 'c5',
                firstName: this.coachFirstName.val,
                lastName: this.coachLastName.val,
                areas: this.coachAreas.val,
                description: this.coachDescription.val,
                hourlyRate: this.coachHourlyRate.val
            }
            this.$emit('save-data', formData);
        },
    }
}
</script>