<template>
    <div>
        <base-card>
            <coach-form @save-data="registerCoach"></coach-form>
        </base-card>
    </div>
</template>

<script>
import CoachForm from '../../components/coaches/CoachForm.vue';

export default {
    components: {
        CoachForm,
    },
    methods: {
        registerCoach(data)
        {
            this.$store.dispatch('registerCoach', data);
            this.$router.replace('/coaches');
        }

    }
}
</script>